import { React, useState } from "react";
import { Form, Button, Col } from "react-bootstrap";
import "./Login.css";
import socket from "./SocketIO";

function Login() {

    let [username, setUsername] = useState("");
    let [key, setKey] = useState("");

    function userLogin(event) {
        event.preventDefault();
        console.log([username, key]);
        socket.emit("login", { x: username, y: key });
        return false;
    }

    return <div className="game-login">
        <h1>الدخول إلى اللعبة</h1>
        <div className="game-login-top">
            <div><span>الدخول</span></div>
            <p>
                عليك تفعيل ملفات التعريف (كوكيز) لتتمكن من تسجيل الدخول. و إذا تعدد مستخدموا هذا الحاسب يجب أن تضغط على تسجيل الخروج في كل مرة تنتهي من اللعب وذلك حفاظاً على بياناتك.
            </p>
        </div>
        <form onSubmit={userLogin}>
            <Form.Group className="game-login-form">
                <Form.Row>
                    <Form.Label column sm={2}>اسم المستخدم</Form.Label>
                    <Col>
                        <Form.Control type="text" name="name" tabIndex={1} size="sm" onChange={e => setUsername(e.target.value)} placeholder="اسم المستخدم" />
                    </Col>
                </Form.Row>
                <Form.Row>
                    <Form.Label column sm={2}>كلمة المرور</Form.Label>
                    <Col>
                        <Form.Control type="password" name="password" tabIndex={2} size="sm" onChange={e => setKey(e.target.value)} placeholder="كلمة المرور" />
                    </Col>
                </Form.Row>
            </Form.Group>
            <Button variant="secondary" type="submit">تسجيل الدخول</Button>
        </form>
    </div>
}

export default Login;